import * as yup from 'yup';

export const mainSchema = yup.object({
  name: yup
    .string()
    .min(3, '     3 ')
    .required("   "),
  email: yup
    .string()
    .email(' email')
    .matches(/\.[A-Z]{2,4}$/i, ' email')
    .required("   "),
  doctorType: yup
    .string()
    .required("   "),
  // description: yup
  //   .string()
  //   .min(6, '   ')
  //   .required("   "),
});

export const loginSchema = yup.object({
  email: yup
    .string()
    .email(' email')
    .matches(/\.[A-Z]{2,4}$/i, ' email')
    .required("   "),
  password: yup
    .string()
    .min(8, '    8 ')
    .matches(/[A-Z]/, '      ')
    .matches(/[a-z]/, '      ')
    .matches(/[0-9]/, '     ')
    .matches(/[!@#$%^&*]/, '     ')
    .required('   '),
});

export const signUpSchema = yup.object({
  name: yup
    .string()
    .min(3, '     3 ')
    .required("   "),
  email: yup
    .string()
    .email(' email')
    .matches(/\.[A-Z]{2,4}$/i, ' email')
    .required("   "),
  password: yup
    .string()
    .min(8, '    8 ')
    .matches(/[A-Z]/, '      ')
    .matches(/[a-z]/, '      ')
    .matches(/[0-9]/, '     ')
    .matches(/[!@#$%^&*]/, '     ')
    .required('   '),
  specialityMain: yup
    .string()
    .required("   "),
});
