import { showToastMessage } from "../../../../utils/showToastifyMessage";

export const onSubmit = (values: Record<string, string | boolean>) => {
  console.log(values);
  fetch('http://89.223.68.190:8080/patient-requests', {
    method: "POST",
    headers: {
      "Content-Type": "application/json"
    },
    body: JSON.stringify(values),
  })
    .then(async (res) => {
      console.log(res);
      if (res.status === 201) {
        showToastMessage("   ", "green");
      } else {
        showToastMessage("-   ,  ", "red");
      }
    }).catch((error) => {
      console.log(error);
      showToastMessage("", "red");
    });
};
